}
.cadastroFechado{
    margin-top:-175px;
    -webkit-animation-name: ani-cadastroFechado;
            animation-name: ani-cadastroFechado;
    -webkit-animation-duration: 500ms;
            animation-duration: 500ms
}

.cadastroAberto{
    margin-top:0px;
    -webkit-animation-name: ani-cadastroAberto;
            animation-name: ani-cadastroAberto;
    -webkit-animation-duration: 500ms;
            animation-duration: 500ms
  }

@-webkit-keyframes ani-cadastroFechado{
    from{ margin-top: 0px }
    to{ margin-top: -175px}
}

@keyframes ani-cadastroFechado{
    from{ margin-top: 0px }
    to{ margin-top: -175px}
}

@-webkit-keyframes ani-cadastroAberto{
    from{ margin-top: -175px}
    to{ margin-top: 0px }
}

@keyframes ani-cadastroAberto{
    from{ margin-top: -175px}
    to{ margin-top: 0px }
}

  
.name-control{
    width: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin:5px;
    padding:5px;
    margin-bottom:10px
}

.name-control span{
    font-size:10px;
    color: #30302D;
    margin-bottom:5px
  }
  
  .name-control input{
    font-size:16px;
    color: #30302D;
    border: none;
    border-bottom: 1px solid #30302D;
  }

  .ProdutoForm{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.ProdutoForm div{
    padding:5px;
    margin: 5px;
}

.ProdutoForm .form-control{
    background: #C9C8E2;
    -webkit-flex: 2 1;
            flex: 2 1;
    border-radius: 5px
}

.ProdutoForm span{
    color: #736ff4;
    display: block;
    font-size:10px;
}
.ProdutoForm input{
    width:75%;
    border-radius: 5px;
    border: 1px solid #30302D;
    color: #30302D;
    font-weight: bold;
    font-size:16px;
    padding:5px;
}
.MainScreen{
    padding-top:20px;
    overflow:hidden
}

.ListaHeader{
  display: -webkit-flex;
  display: flex
}

.ListaHeader h2{
  -webkit-flex:2 0 auto;
          flex:2 0 auto
}

.ListaHeader button{
  -webkit-flex:1 0 auto;
          flex:1 0 auto;
  float: right 
}
  
.blur{
    -webkit-filter: blur(2px);
            filter: blur(2px);
}



.Produto{
    margin:5px;
    -webkit-flex: 1 1;
            flex: 1 1;
    border-radius:10px;
    box-shadow: 0px 0px 1px 0px rgba(0,0,0,0.45);
    -webkit-animation-name: aniLista;
            animation-name: aniLista;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
  
  }
  
  .Produto .header{
    font-size: 10px;
    background: #30302D;
    color: #DB5461;
    padding:5px;
    font-style: italic;
  }
  
  .Produto .body{
    font-size:24px;
    background: #DB5461;
    color: #30302D;
    padding:5px
  }
  
  .Produto .info{
    font-size:12px
  }
  
  .Produto .result{
    float:right
  }

  .lista{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column
  }

  .ListaForm{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-animation-name: aniDialogo;
            animation-name: aniDialogo;
    -webkit-animation-duration: .5s;
            animation-duration: .5s
  }
  
  .ListaForm .wrapper{
    border: 1px solid #af5c0e;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: auto 5%;
    background: #FFFFFF;
    position: absolute;
    width: 80%;
    padding: 5%;
    z-index:1;
    border-radius: 5px
  }
  
  .ListaForm .bg{
    width:100%;
    height:100%;
    position: absolute;
    top:0px;
    left:0px;
    background: #999999;
    opacity: 0.5;
  }

  @-webkit-keyframes aniLista{
    from{ opacity: 0; -webkit-transform:translateY(-10px); transform:translateY(-10px)}
    to{ opacity : 1; -webkit-transform:translateY(0px); transform:translateY(0px)}
  }

  @keyframes aniLista{
    from{ opacity: 0; -webkit-transform:translateY(-10px); transform:translateY(-10px)}
    to{ opacity : 1; -webkit-transform:translateY(0px); transform:translateY(0px)}
  }
  
  @-webkit-keyframes aniDialogo{
    from{ opacity: 0; -webkit-transform:scale(0.2); transform:scale(0.2)}
    to{ opacity : 1; -webkit-transform:scale(1); transform:scale(1)}
  }
  
  @keyframes aniDialogo{
    from{ opacity: 0; -webkit-transform:scale(0.2); transform:scale(0.2)}
    to{ opacity : 1; -webkit-transform:scale(1); transform:scale(1)}
  }
